<template>
  <b-container>
    <MyHeader></MyHeader>
    <router-view></router-view>
    <MyFooter></MyFooter>
    <b-modal
      id="root-modal"
      :visible="modalShow"
      @hide="setModalShowFalse"
      ref="root-modal"
      hide-footer
      title="Demo Site"
    >
      <div class="d-block text-center">
        <h4>{{ modalMsg }}</h4>
      </div>
    </b-modal>
  </b-container>
</template>

<script>
import MyFooter from "@/components/MyFooter.vue";
import MyHeader from "@/components/MyHeader.vue";
import { mapGetters } from "vuex";

export default {
  name: "Root",
  data() {
    return {};
  },
  components: {
    MyHeader,
    MyFooter,
  },
  mounted() {},
  computed: {
    ...mapGetters("modal", ["modalShow", "modalMsg"]),
  },
  methods: {
    // https://stackoverflow.com/questions/49427898/managing-which-bootstrap-vue-modal-is-open-through-vuex-state
    setModalShowFalse() {
      this.$store.dispatch("modal/setModalShow", false);
    },
  },
};
</script>